          @import "@/assets/sass/mixins/_clearfix.scss";
          @import "@/assets/sass/mixins/_truncate.scss";
          @import url(https://static.gabia.com/fonts/icons/v1.0.1/style.css);
          










































































@import '@/assets/sass/base/_reset.scss';
@import '@/assets/sass/mixins/_triangle.scss';

.gt-tooltip {
  position: relative;
  display: inline-block;
  
  .gt-arrow {
    position: absolute;
  }

  .gt-tt-content {
    //display: none;
    position: absolute;
    box-sizing: border-box;
    padding: 6px 8px;
    font-size: 12px;
    color: $tooltip-font;
    border-radius: 4px; 
    min-width: 100px;
    background: $primary;    
    text-align: center;
    user-select: none;
    cursor: default;
    z-index: 1000;

    &.gt-tooltip-show {
      animation: aniTooltipVisible .1s forwards;
      @keyframes aniTooltipVisible {
        from { transform: scale(0); opacity: 0 }
        to { transform: scale(1); opacity: 1 }
      }
    }
    &.gt-tooltip-hide {
      animation: aniTooltipIvisible .1s forwards;
      @keyframes aniTooltipIvisible {
        from { transform: scale(1); opacity: 1 }
        to { transform: scale(0); opacity: 0 }
      }
    }

  }

  &[data-placement="right"] {
    .gt-triangle {
      position: absolute;
      @include triangle(left, 6px, 6px, $primary);
    }
  }

  &[data-placement="left"] {
    .gt-triangle {
      position: absolute;
      @include triangle(right, 6px, 6px, $primary);
    }
  }

  &[data-placement="bottom"] {    
    .gt-triangle {
      position: absolute;
      @include triangle(top, 6px, 6px, $primary);
    }
  }

  &[data-placement="top"] {
    .gt-triangle {
      position: absolute;
      @include triangle(bottom, 6px, 6px, $primary);
    }
  }

  .gt-triangle {
    position: absolute;
    @include triangle(bottom, 6px, 6px, $primary);
  }

  $types: (
    'primary' : $primary,
    'warning' : $warning,
    'danger' : $danger,
    'info' : $info,
    'secondary' : $secondary,
    'error' : $error,
    'importance' : $importance,
    'white' : $white,
    'attention' : $attention,
  );

  @each $key, $colorType in $types {

    &.gt-tooltip-#{$key} {
      .gt-tt-content {
        background: #{$colorType};
      }
      &[data-placement="right"] {
        .gt-triangle {
          @include triangle(left, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="left"] {
        .gt-triangle {
          @include triangle(right, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="bottom"] {
        .gt-triangle {
          @include triangle(top, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="top"] {
        .gt-triangle {
          @include triangle(bottom, 6px, 6px, #{$colorType});
        }
      }
    }
  }

}
